.text-white {
  color: #fff;
}


.vsl {
  position: relative;
  overflow: hidden;
  max-width: 770px; // Largura máxima de 700px
  margin: 15px auto; // Centraliza a div
  width: 100%; // Torna a largura responsiva
  &:before {
    content: "";
    display: block;
    padding-top: 56.25%; // Proporção 16:9
  }

  video, embed, iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 10px;

  }
}

.separator-1 {
  color: #169f8a;

  svg {
    path {
      fill: currentColor;
    }
  }
}

.block-conversion {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 25px;

  span {
    color: #9da0a4;
    font-weight: 400;
    font-size: 0.95em;
    width: 100%;
    text-align: center;
  }

  img {
    filter: grayscale(1);
    opacity: 0.5;
    width: 100%;
  }
}

.separator-rocket {
  margin: 0 auto;
  width: 100%;
  max-width: 300px;
  position: relative;
  color: #149d8c;
  text-align: center;
  margin-bottom: 20px;

  svg {
    fill: currentColor;
    padding: 6px;
    border: 2px solid currentColor;
    border-radius: 50%;
  }

  &:after {
    content: "";
    width: 100px;
    height: 1.5px;
    background: currentColor;
    position: absolute;
    left: 15px;
    bottom: 50%;
  }

  &:before {
    content: "";
    width: 100px;
    height: 1.5px;
    background: currentColor;
    position: absolute;
    right: 15px;
    bottom: 50%;
  }

}


div.carousel-nav-icon > svg {
  height: 40px;
  width: 40px;
  fill: #9da0a4;
}

.carousel-component {
  margin: 0 auto;
  width: 100%;
  .carousel-item {
    .card {
      background: transparent;
      border: 0;
      margin: 3px;
    }
  }
}

@media (max-width: 767px) {
  .carousel-inner .carousel-item > div {
    display: none;
  }
  .carousel-inner .carousel-item > div:first-child {
    display: block;
  }
}

.carousel-inner .carousel-item.active,
.carousel-inner .carousel-item-next,
.carousel-inner .carousel-item-prev {
  display: flex;
}

/* medium and up screens */
@media (min-width: 768px) {

  .carousel-inner .carousel-item-end.active,
  .carousel-inner .carousel-item-next {
    transform: translateX(33%);
  }

  .carousel-inner .carousel-item-start.active,
  .carousel-inner .carousel-item-prev {
    transform: translateX(-33%);
  }
}

.carousel-inner .carousel-item-end,
.carousel-inner .carousel-item-start {
  transform: translateX(0);
}
