@media only screen and (max-width: 600px) {
  .logo img {
    display: block !important;
    max-width: 140px;
    margin: 0 auto;
  }

  .bg-hero{
    min-height: 500px;
  }

  video{
    max-width: 100%!important;
    border-radius: 5px;
  }

  #alunos{
    .flex-title{
      padding-top: 20px!important;
    }
  }

  .for-peoples{
    margin-bottom: 20px!important;
    .card{
      margin-bottom: 15px!important;
    }
  }
  //.menu {
  //  display: none;
  //}
  .bg-hero {
    .headline {
      margin: 15px auto;

      h1 {
        font-size: 23px;
        line-height: 1.2;
      }

      p {
        font-size: 17px;
      }
    }
  }




  .flex-items-list-course {
    padding: 8px;

    div {
      width: calc(50% - 10px) !important;
      margin: 0 5px;
    }
  }

  .custom-size {
    height: auto !important;
  }

  .shape-bottom svg {
    width: 250vw;
    height: 40px;
  }

  .section {
    flex-wrap: wrap;
    padding: 16px;
    margin: 0 auto;
  }

  .oferta{
    padding: 10px;
    .price p{
      padding: 5px 15px;
      font-size: 1.2em!important;
    }
  }

  #oferta h1{
    font-size: 26px;
    line-height: 33px;
  }

  .head-title{
    font-size: 1.6em!important;
  }

  .sub-title{
    font-size: 1.2em;
    margin-bottom: 8px;
  }

  .bonus{
    h1.head-title{
      font-size: 24px!important;
      line-height: 33px;
    }
    p{
      font-size: 1em!important;
    }
  }

  #cert{
    h1{
      font-size: 26px;
      line-height: 33px;
    }
    h2{
      font-size: 15px;
      color: #4f4e4e;
    }
  }

  #garantia{
    font-size: 0.85em;
  }


  .btn-conversion {
    min-width: 80%;
    max-width: 90%!important;
    padding: 12px;

  }
}
