@import "variables";

&.magus-pv1{
  margin: 0;
  font-family: 'IBM Plex Sans', sans-serif!important;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  overflow-x: hidden;
}

.menu {
  position: fixed;
  top: 0;
  left: 0px;
  right: 0;
  z-index: 1000;
  display: flex;
  flex-direction: row;
  gap: 40px;
  align-items: center;
  justify-content: center;
  .hamburger {
    display: none;
    cursor: pointer;
    width: 32px;
    height: 32px;
    position: absolute;
    top: 15px;
    right: 15px;
    background: #fff;
    border: 0px;
    box-shadow: 0px 2px 5px #807878;
    border-radius: 50%;

    span {
      display: block;
      width: 80%;
      height: 2px;
      background-color: gray;
      margin: 3px auto;
      position: relative;
      transition: background-color 0.3s ease, transform 0.3s ease, top 0.3s ease, bottom 0.3s ease;
    }

    &.open {
      span {
        background-color: #000;

        &:nth-child(1) {
          top: 1px;
          transform: rotate(45deg);
          margin: 0 auto;
        }

        &:nth-child(2) {
          top: -1px;
          transform: rotate(-45deg);
          margin: 0 auto;

        }

        &:nth-child(3) {
          display: none;
        }
      }
    }
  }

  ul {
    list-style: none;
    display: flex;
    margin: 5px;
    padding: 10px;

    li {
      a {
        font-weight: 500;
        font-size: 1.2em;
        line-height: 17px;
        color: #d0cdcd;
        margin: 0 25px;
        text-decoration: none;
      }
    }
  }


  @media screen and (max-width: 600px) {
    ul{
      display: none;
    }
  }


 &.active{
   @media screen and (max-width: 600px) {
     .hamburger {
       display: block;
     }

     ul {
       display: none;
       position: fixed;
       top: 0;
       left: -50%;
       width: 50%;
       height: 100%;
       background-color: #fff;
       flex-direction: column;
       padding: 50px;
       transition: left 0.3s ease;
     }

     &.responsive {
       ul {
         display: flex;
         left: 0;
         position: fixed;
         width: 150px;
         background: #fff;
         height: 100vh;
         justify-content: center;
         align-items: center;
         flex-wrap: wrap;
         flex-direction: column;
         padding: 15px;
         margin: 0;
         border-right: 1px solid #ededed;
         box-shadow: 1px 0px 4px #b0abab;
         li{
           margin: 10px 0;
           width: 100%;
           padding: 6px;
           a{
             text-align: left!important;
             color: #383737;
           }
         }
       }
     }
   }
 }
}


.bg-hero {
  padding: 15px;
  background-color: transparent;
  background-image: $bg;
  position: relative;
  min-height: 750px;

  .headline {
    max-width: 800px;
    margin: 66px auto auto;

    h1 {
      color: #fff;
      font-family: 'IBM Plex Sans', sans-serif!important;

      margin-top: 33px;
      margin-bottom: 24px;
      text-align: center;
      font-size: 38px;
      line-height: 46px;
      font-weight: 800;
    }

    p {
      color: #fff;
      font-style: normal;
      font-weight: 400;
      font-size: 21px;
      line-height: 23px;
      letter-spacing: -0.3px;
      max-width: 650px;
      margin: 0 auto;
      padding: 0px;
      text-align: center;
    }
  }
}


.shape-bottom {
  position: absolute;
  bottom: 0;
  transform: rotate(180deg);
  left: 0;
  overflow: hidden;
  width: 100%;

  &.top {
    top: 0 !important;
    transform: rotate(0deg) !important;
  }

  svg {
    width: 100vw;
    height: 117px;
    display: block;
    position: relative;
    margin-top: -12px;
    transform: translateX(-50%);
    left: 50%;

    path {
      fill: #fff;
      transform-origin: center;
    }
  }
}




nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: .3s;
  z-index: 999;
  &.active{
    background: rgba(17, 0, 11, 0.6);
    backdrop-filter: blur(12.1px);
  }
}
