body {
  background: #f8f8f8;
}

$gradient: linear-gradient(to right, #3cc871, #11998e);
.hero {
  text-align: center;
  color: #fff;
  padding: 20px;

  &.gradient-pv2 {
    background: $gradient;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: auto;
  }

  .sub-headline {
    font-size: 1.5em;
    color: #fff;
    max-width: 500px;
    margin: 0 auto;
  }

  .headline {
    max-width: 1000px;
    margin: 0 auto;

    h1 {
      font-weight: 800;
      font-size: 2.3em
    }

    p {
      font-size: 1.2em;
      margin: 20px 0 0 0;
    }
  }
}

#oquee {
  background: #ffffff;
  position: relative;
  padding: 25px;

  .container {
    display: flex;
    justify-content: space-between;
    align-content: center;
    padding: 60px 0;
    align-items: center;

    .colum {
      width: 100%;
      margin: 16px;
    }

    h1 {
      font-weight: 800;
      color: #54595f;

      span {
        color: #139c8c;
        text-transform: capitalize;
      }
    }

    p {
      font-size: 1.2em;
      color: #54595f;
      text-align: left;
      line-height: 1.7;
    }

  }
}

#conteudo {
  margin-top: 15px;

  .headline {
    h1 {
      font-weight: bold;
      font-size: 2.2em;
      color: #54595f;
    }

    p {
      font-size: 1.5em;
      font-weight: 300;
    }
  }

  .cards {
    display: flex;
    flex-wrap: wrap;

    .card {
      background: #fff;
      margin: 20px;
      padding: 18px;
      width: calc(33.33% - 40px);
      border-radius: 5px 45px 5px 45px;
      border: 0px;
      box-shadow: 0px 0px 30px 20px #f2f2f2;

      h2 {
        color: #54595f;
        font-size: 1.5em;
        font-weight: 700;
      }

      p {
        color: #9da0a4;
        font-size: 1.1em;
        line-height: 1.5;
        font-weight: 400;
      }

      img {
        max-width: 90px;
      }
    }
  }
}

#professor {
  .container {
    display: flex;
    padding: 60px 0;
    align-items: center;
    margin: 0 auto;
    max-width: 1100px;

    .desc {
      margin: 16px;
      width: 65%;

      p {
        color: #4f4e4e;
        font-size: 1.11em;
        font-weight: 400;
      }
    }

    .image {
      width: 35%;

      img {
        border-radius: 8px;
      }
    }

    h2 {
      color: #54595f;
      font-size: 1.5em;
      font-weight: 700;
    }
  }

}


#cardsBg {
  background: $gradient;
  position: relative;
  padding: 60px 0;

  .cards {
    display: flex;
    flex-wrap: wrap;
    margin: 50px;
    .card {
      margin: 15px;
      padding: 22px;
      width: calc(33.33% - 30px);
      border-radius: 16px;

      .desc {
        display: flex;
        align-items: center;
        justify-content: space-between;

        h2 {
          text-align: left;
          color: #4f4e4e;
          font-weight: 700;
          font-size: 1.6em;
        }

        img {
          margin: 0 15px;
          max-width: 70px;
        }
      }

      p {
        text-align: left;
        color: #717070;
        font-size: 1.2em;
        line-height: 1.6;
      }
    }
  }

  .custom-shape-divider-bottom-1683497721 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
  }

  .custom-shape-divider-bottom-1683497721 svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 40px;
  }

  .custom-shape-divider-bottom-1683497721 .shape-fill {
    fill: #fff;
  }

  .custom-shape-divider-top-1683497768 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
  }

  .custom-shape-divider-top-1683497768 svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 40px;
  }

  .custom-shape-divider-top-1683497768 .shape-fill {
    fill: #F8F8F8;
  }
}

#duvida {
  position: relative;
  padding: 50px;
  background: #fff;

  .content {
    max-width: 1100px;
    margin: 0 auto;
    text-align: center;

    .headline {
      h1 {
        color: #54595f;
        font-weight: bold;

        span {
          color: #139c8c;
        }
      }

      h2 {
        color: #4f4e4e;
        font-weight: 300;
      }
    }

    p {
      color: #767575;
      font-size: 1.15em;
    }
  }
}

#paraquem {
  padding: 50px;

  .headline{
    h1{
      color: #54595f;
      font-weight: 700;
      span{
        color: #139c8c!important;
      }
    }
    p{
      color: #878282;
      font-weight: 300;
      font-size: 1.3em;
      margin-top: 15px;
    }
  }

  .cards{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .simple-card{
      margin: 15px;
      width: calc(33.33% - 30px);
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 12px 30px;
      box-shadow: 1px 1px 20px #ebe2e2;
      min-height: 150px;
      h3{
        color: #4f4e4e;
      }
      img{
        max-width: 70px;
        margin: 20px;
      }
    }
  }

}

.btn-conversion {
  font-size: 1.5em;
  font-weight: 600;
  text-shadow: 0px 0px 10px rgba(23, 77, 0, 0.91);
  fill: #fff;
  color: #fff;
  background-color: transparent;
  background-image: linear-gradient(30deg, #ff6641 56%, #ff7925 100%);
  border-style: solid;
  border-width: 0 0 4px;
  border-color: #bd391f;
  border-radius: 15px;
  padding: 16px 20px;
  width: 100%;
  margin: 12px auto;
  text-decoration: none;
  display: block;
  max-width: 770px;
  transition: ease-in 0.3s;

  &.v2 {
    background: linear-gradient(to right, #3cc871, #11998e);
    border-color: #11998e;
  }

  &:hover {
    border-color: transparent;
    box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.5);
    transform: scale(1.1);
  }
}


.custom-shape-divider-bottom-1683417921 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-bottom-1683417921 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 61px;
  transform: rotateY(180deg);
}

.custom-shape-divider-bottom-1683417921 .shape-fill {
  fill: #f8f8f8;
}


.headline {
  text-align: center;
  h1 {
    font-weight: bold;
    font-size: 2.2em;
    color: #54595f;
  }

  p {
    font-size: 1.5em;
    font-weight: 300;
  }
}
