@import "variables";

img {
  max-width: 100%;
  width: 100%;
}


.bg-primary {
  background: $bg;

  p, h1, h2 {
    color: #fff !important;
  }
}

.relative {
  position: relative;
}

.video-auto {
  max-width: 100%;
  max-height: 100%;
  aspect-ratio: 11/9;
  position: relative;
}

.btn-conversion {
  color: #fff;
  font-size: 1.3em;
  line-height: 1.5;
  text-shadow: 0px 6px 14px rgba(0, 0, 0, 0.25);
  background: rgb(255, 220, 0);
  border-radius: 329px;
  padding: 16px 31px;
  margin: 15px auto;
  font-weight: 700;
  color: #000;
  box-shadow: rgb(255, 178, 0) 0px 5px 0px;
  text-align: center;
  position: relative;
  min-width: 250px;
  text-decoration: none;
  overflow-wrap: unset;
  text-align: center;
  justify-content: center;
  display: flex;
  max-width: 450px !important;
  transition: box-shadow 0.2s ease-in-out; /* transição */

  &:hover {
    color: #4f4e4e;
    box-shadow: rgb(255, 178, 0) 0px 5px 0px;
  }

  &:active {
    box-shadow: inset rgb(255, 178, 0) 0px -5px 0px !important;
  }
}


.custom-size {
  height: 500px;
  display: flex;
  justify-content: center;
}

.text-center {
  text-align: center;
}

.full-width {
  width: 100%;
}

.head-title {
  font-size: 2em !important;
  font-weight: 700;
  color: #5c003e;
  max-width: 920px;
  margin: 20px auto;
  width: 100%;

  p {
    color: #020408;
  }
}


.wrap {
  flex-wrap: wrap !important;
}

.mt-15 {
  margin-top: 15px;
}

.mt-30 {
  margin-top: 30px;
}

.lists {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  .item {
    display: flex;
    align-items: center;
    justify-content: center;

    .icon {
      background: $bg;
      border-radius: 50%;
      width: 35px;
      min-width: 35px;
      min-height: 35px;
      height: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 5px;
      margin-right: 20px;
      i {
        color: #fff;
        font-size: 1.2em;
      }
    }

    img {
      max-width: 55px;
      width: 100%;
      min-width: 55px;
      margin-right: 20px;
    }
  }
}

.flex-title {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}

.white {
  color: #fff;
}

.sub-title {
  max-width: 1000px;
  margin: 0 auto;
}

.light {
  font-weight: 300;
}

.flex-items-list-course {
  max-width: 1000px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 25px;

  div {
    width: calc(25% - 10px);
    margin: 0 5px;
  }

  a {
    display: flex;
    justify-content: center;
    color: #fff;
    text-decoration: none;

    i {
      width: 50px;
      height: 50px;
      color: #fff;
      border-radius: 50%;
      border: 1px solid #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  p {
    font-size: 0.9em;
  }
}


.mb-30 {
  margin-bottom: 30px;
}

video {
  width: 100%;
  max-width: 800px;
  height: auto;
  margin: 0 auto;
}

.bonus {

  img {
    max-width: 200px !important;
    margin: 0 auto;
  }

  .section {
    padding: 0px;
  }
}

.video-responsive {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 15px;
  }
}

#deps {
  .col {
    width: 100%;
    position: relative;
  }
}

.fixed-bottom-right {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 350px;
  max-width: 75%;
  height: 190px;
  z-index: 999;
  aspect-ratio: 16/9;
}

.close-btn {
  display: none;
  position: absolute;
  top: -30px;
  right: -0px;

  font-size: 30px;
  font-weight: bold;
  color: #000;
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
  z-index: 1000;
  .ct{
    width: 23px;
    text-shadow: 0px 0px 1px #f7f7f7;
    height: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.fixed-bottom-right .close-btn {
  display: block;
}


#oferta {
  .alert-ofert{
    color: #f7f7f7;
    padding: 0 25px;
  }
  h1 {
    font-weight: 700;
    font-size: 42px;
    text-transform: capitalize !important;
    line-height: 55px;
  }

  p {
    font-style: normal;
    font-weight: 400;
    font-size: 19px;
    line-height: 28px;
    letter-spacing: -0.3px;
  }


  .oferta {
    max-width: 600px;
    width: 100%;
    margin: 8px auto;
    border-radius: 10px 10px 0 0;
    background: #020b14;
    box-shadow: 0px 0px 17px #bcbabafa;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;

    img {
      max-width: 180px;
    }

    .price {
      color: #f7f7f7;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      align-items: center;
      flex-direction: column;

      p {
        margin: 0;
        font-size: 1.4em;
        color: #e6e6e6;
      }

      h1 {
        margin: 5px 0;
        text-align: center;
        font-size: 38px;
        line-height: 1;
        font-weight: 700;
      }
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        font-size: 1.4em;
        color: #f7f7f7;

        i {
          margin: 0 8px;
          color: #00ab6b;
        }
      }
    }

  }
}

.bg-black {
  background: #020b14;
}

.padding-bottom-detail {
  padding-bottom: 50px;
}

.progress-matriculas {
  background: #bcbabafa;
  padding: 2px 10px;
  border-radius: 0px;
  position: relative;
  max-width: 580px;
  margin-top: -8px;
  width: 100%;

  span {
    position: relative;
    z-index: 99;
    color: #fff;
  }

  .progress {
    background: #00ab6b;
    position: absolute;
    z-index: 9;
    width: calc(97% - 16px);
    left: 0;
    top: 0;
    border-radius: 0;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 8px;
    color: #fff;
  }
}

#garantia {
  font-size: 1.2em;
}

footer {
  background: #000000;
  color: #f7f7f7 !important;

  p {
    color: #f7f7f7 !important;
  }

  a {
    color: #f7f7f7;
    text-decoration: none;
    margin-bottom: 15px !important;
  }

  .disclaimer {
    font-size: 0.7em;


  }
}


.for-peoples{
  padding: 0 20px;
  margin-bottom: 120px;
  .card{
    margin: 0 15px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    min-height: 250px;
    border-radius: 10px;
    box-shadow: 0px 2px 9px rgba(2, 11, 20, 0.26);
    h2{
      margin: 5px 0;
    }
    p{
      margin: 0;
      line-height: 1.4;
      font-size: 0.9em;
    }
    img{
      margin: 0 auto;
      max-width: 70px;
    }
  }
}
