swiper-container {
  width: 100%;

  img {
    max-width: 100%;
  }

  swiper-slide {
    width: 100% !important;
    img{
      border-radius: 10px;
    }
  }

  .swiper-backface-hidden .swiper-slide {

  }
}

.mySwiper2 {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
}

.mySwiper2 swiper-slide {
  width: 20%!important;
  height: 100%;
  opacity: 0.8;
}

.mySwiper2 .swiper-slide-thumb-active {
  opacity: 1;
}
