#faq {
  padding: 23px;

  h1 {
    font-size: 1.2em;
    margin: 0;
    color: #f7f7f7;
    padding: 0;
  }

  .accordion {
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: hidden;
    max-width: 650px;
    width: 100%;
    margin: 0 auto;
    color: #f7f7f7;
  }

  .accordion-item {
    background-color: transparent;
    border-bottom: 1px solid #ccc;
  }

  .accordion-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    padding: 20px;

    h3 {
      margin: 0;
    }

    i {
      transition: transform 0.3s ease;
    }

    &.open i {
      transform: rotate(180deg);
    }
  }

  .accordion-content {
    padding: 0 20px;
    height: 0;
    overflow: hidden;
    transition: height 0.3s ease, padding 0.3s ease;

    p {
      margin: 0;
    }

    &.open {
      padding: 12px 20px;
      height: auto;
    }
  }
  padding-bottom: 120px;
}
